.App {
  text-align: center;
}

h1 ,h2 ,h3 ,h4 ,h5 ,h6 ,p , a , label{
  color: #FFFFFF;
font-family: Lato;
}
.info {
  left: 10.4%;
  display: flex;
  top: 11.09%;
  justify-content: center;
  flex-wrap: wrap;

}

.info__img {
  padding: 1rem;
}
.info__img img{
  height: 20rem;
  border: 2mm ridge rgba(211, 220, 50, .6);
  border-radius: 0.25rem;

}

.info__text {
  margin: 1rem;
 min-width: 10rem;
 max-width: 35rem;
 bottom: 14.54%;
 font-family: 'Inter';
 font-style: normal;
 font-weight: 400;
 font-size: 20px;
 line-height: 24px;
}

.Navi {
  font-size: 2rem;
  display: flex;
  height: 18rem;
  flex-direction: column;

 }

.contact--title {
  display: block;
  text-align: center;
}
.inputs {
  max-width: 600px;
  margin: auto;
  padding: 0 10px;
  overflow: hidden;
 
  font-size: 1.5rem;

}
.inputs label {


font-family: 'Inika';
font-style: normal;
font-weight: 400;
line-height: 23px;

color: #FFFFFF;


}

.inputs input {
  display: block;
  width: 100%;
  box-sizing: border-box;
  margin: 16px 0;
  border-radius: 0.65rem;
  border: 0;
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2)), rgba(255, 248, 248, 0.32);
  padding: 20px 40px;
  outline: none;
  color: #ddd;
  transition: 0.5s;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}
.inputs input:focus {
  box-shadow: 0 0 10px 4px #34495e;
}
.inputs input::placeholder {
  font-size: 1.3rem;
}

.social {
  margin-top: 1rem;
}
 .social__media {
  display: flex;
    justify-content: center;
    text-decoration: none;
    list-style-type: none;
    flex-wrap: wrap;
  }
  .social__media li a {
    color: #FFFFFF;
    margin-left: 1rem;
    margin-right: 1rem;
  }
  .social__media img {
    width: 50px;
    height: 50px; 
       box-shadow: 0 0 8px #1c32aa;
    border-radius: 50%;
    padding: 0.5rem;
    transition: 0.5s;
    filter: invert(1);
  }
  .social__media img:hover {
    fill: #e27e7e;
    filter: invert(0.45);


  }

  .projectDetail {
    height: 12rem;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 12vw;
  }
  .projectDetail-list {
    display: flex;
    justify-content: center;
    align-items: center;
    list-style-type: none;
  }
  .projectDetail-li {
    padding: 1rem;
    background-color: #e5b779;
    text-align: center;
    margin: 1rem;
    border-radius: 0.3rem;
    box-shadow: 4px 2px 12px 4px red;
    color:black;
    transition: 0.5s;
  }
  .projectDetail-link {
      font-size: 2rem;
      text-decoration: none;
      color: white;
      cursor: pointer;
  }
 
  .projectDetail-li:hover {
    box-shadow: 8px 8px 8px 2px #e75c5c;
  }
  .projectDetail-li:focus {
    border: 2px solid;
  }
  .projectDetail-2 {
    background-color: #e27e7e;
  }
   .projectDetail-2:hover {
    box-shadow: 8px 8px 8px 2px #e5b779;
  }
  .main {
    padding: 1rem 0 0 0;
    margin-top: 6rem !important;
  }


  .button-wrapper {
  margin-top: 1.5rem;
  text-align: left;
}

.btn-primary {
  display: inline-block;
  padding: 0.75rem 1.5rem;
  background-color: #0a84ff;
  color: #fff;
  font-weight: bold;
  border-radius: 0.5rem;
  text-decoration: none;
  transition: background-color 0.3s ease, transform 0.2s ease;
  box-shadow: 0 4px 10px rgba(0,0,0,0.2);
}

.btn-primary:hover {
  background-color: #006fdd;
  transform: translateY(-2px);
}
